<template>
  <div class="umis-attrs-editor">
    <el-tabs
      v-model="activeTab"
      class="umis-attrs-editor__tabs"
      @tab-click="onTabChange"
      :before-leave="beforeLeave"
    >
      <el-tab-pane label="Â∏∏ËßÑ" name="0"/>
      <el-tab-pane label="Êï∞ÊçÆ" name="1"/>
      <el-tab-pane label="ÂàùÂßãÊé•Âè£" name="2"/>
      <el-tab-pane label="ÊòæÈöê" name="3"/>
      <el-tab-pane label="Â≠êËäÇÁÇπ" name="4"/>
    </el-tabs>
    <template v-if="visibleTabs[activeTab]">
      <div class="umis-attrs-editor__contents">
        <el-tabs
          v-if="activeTab === '4'"
          v-model="childType"
          tab-position="right"
          type="border-card"
          class="umis-attrs-editor__nodes"
          @tab-click="onTabChange"
          :before-leave="beforeLeave"
        >
          <el-tab-pane v-if="visibleNodes.header" label="Â§¥ÈÉ®" name="header"/>
          <el-tab-pane v-if="visibleNodes.body" label="‰∏ª‰Ωì" name="body"/>
          <el-tab-pane v-if="visibleNodes.footer" label="Â∫ïÈÉ®" name="footer"/>
          <el-tab-pane v-if="visibleNodes.controls" label="Ë°®ÂçïÈ°π" name="controls"/>
          <el-tab-pane v-if="visibleNodes.columns" label="Ë°®Ê†ºÂàó" name="columns"/>
          <el-tab-pane v-if="visibleNodes.panels" label="Èù¢Êùø" name="panels"/>
          <el-tab-pane v-if="visibleNodes.actions" label="Ë°å‰∏∫ÁªÑ" name="actions"/>
        </el-tabs>
        <div
          v-if="!loading"
          class="umis-attrs-editor__content"
          :class="{
            nodes: activeTab === '4'
          }"
        >
          <mis-form
            ref="normalFormRef"
            size="small"
            label-width="110px"
            :path="`${path}/props-editor/${activeTab}/node/${childType}`"
            :init-data="formdata"
            :controls="controls"
          />
        </div>
      </div>
    </template>
    <el-empty v-else :description="`${activeRenderer}Ê∏≤ÊüìÂô®‰∏çÊîØÊåÅËØ•ÈÖçÁΩÆ`" />
    <el-drawer
      v-model="showChildNode"
      title="Â≠êËäÇÁÇπÁºñËæë‰∏≠..."
      size="50%"
      destroy-on-close
      lock-scroll
      :before-close="submitForm"
      custom-class="umis-toolmaker__drawer__container"
    >
      <div class="umis-toolmaker__drawer__content attrs-editor">
        <mis-form
          ref="childFormRef"
          size="small"
          label-width="110px"
          :path="`${path}/props-editor/${activeTab}/node/${childType}`"
          :init-data="childFormdata"
          :controls="childControls"
        />
      </div>
      <div class="umis-toolmaker__drawer__footer">
        <el-button
          size="mini"
          type="primary"
          @click="showChildNode = false"
        >
          Âèñ Ê∂à
        </el-button>
        <el-button
          size="mini"
          type="danger"
          @click="submitForm"
        >
          {{ loading ? 'Êèê‰∫§‰∏≠ ...' : 'Á°Æ ÂÆö' }}
        </el-button>
      </div>
    </el-drawer>
  </div>
</template>
<script>
import {defineComponent, getCurrentInstance, onBeforeMount, ref, onBeforeUnmount, nextTick, onMounted} from 'vue';
import { ElTabs, ElTabPane, ElEmpty } from 'element-plus';
import {cloneDeep} from 'lodash';
import MisForm from '../../renderer/component/form/form.vue';
import { initData } from '../attrs/init-data.js';
import { initApi } from '../attrs/init-api.js';
import { visible } from '../attrs/visible.js';
import { childNode } from '../attrs/child-node.js';

export default defineComponent({
  name: 'AttrsEditor',
  components: {
    ElTabs,
    ElTabPane,
    ElEmpty,
    MisForm,
  },
  props: {
    path: {
      type: String,
      required: true,
    },
    misName: {
      type: String,
      required: true,
    },
    activeRenderer: {
      type: String,
      required: true,
    },
    activeJson: {
      type: Object,
      required: true,
    },
    editTabChange: {
      type: Function,
      required: true,
    },
    nodeChange: {
      type: Function,
      required: true,
    }
  },
  setup(props) {
    const { proxy } = getCurrentInstance();
    const loading = ref(false);
    const childType = ref('body');
    const showChildNode = ref(false);
    const formdata = ref();
    const childFormdata = ref();
    const controls = ref();
    const childControls = ref();
    const activeTab = ref('0');
    const tabs = {
      'mixinProps': '0',
      'initData': '1',
      'initApi': '2',
      'visible': '3'
    };
    const visibleTabs = {};
    const visibleNodes = {};
    let eachFormdata = {};
    let attrs = [initData, initApi, visible, childNode];
    const updateForm = () => {
      let temps;
      controls.value = [];
      temps = activeTab.value === '4' ? attrs[activeTab.value][childType.value] : attrs[activeTab.value];
      temps.forEach(item => {
        formdata.value[item.name] = props.activeJson[item.name] || item.defaultValue;
      });
      Object.assign(formdata.value, eachFormdata);
      controls.value = temps;
    };
    const beforeLeave = () => {
      const form = proxy.$refs.normalFormRef;
      if (form && form.state && form.state.data) {
        eachFormdata = form.state.data;
      }
    };
    const onTabChange = target => {
      const tabIndex = Number(target.props.name);
      if (tabIndex === tabIndex) {
        props.editTabChange(target.props.name);
      } else {
        props.nodeChange(target.props.name);
      }
      loading.value = true;
      nextTick(() => {
        updateForm();
        loading.value = false;
      });
    };
    const onEditDeep = (index) => {
      const {form: {model}} = proxy.$refs.normalFormRef.$refs;
      showChildNode.value = true;
      childFormdata.value = model[childType.value][index];
      childControls.value = proxy.$.appContext.$misAttrs[childFormdata.value.renderer].default;
      proxy.$.appContext.$misAttrs[childFormdata.value.renderer].default.forEach(item => {
        if (!childFormdata.value.hasOwnProperty(item.name)) {
          childFormdata.value[item.name] = item.defaultValue;
        }
      });
    };
    const submitForm = () => {
      const { form } = proxy.$refs.childFormRef.$refs;
      let pureForm = {};
      form.validate((valid) => {
        if (valid) {
          const { model } = form;
          for (let key in model) {
            if (model.hasOwnProperty(key) && model[key] !== '') {
              pureForm[key] = model[key];
            }
          }
          Object.assign(childFormdata.value, pureForm)
          showChildNode.value = false;
        }
      });
    };
    onBeforeMount( () => {
      let nodeList = ['header', 'body', 'footer', 'controls', 'columns', 'panels', 'actions'];
      formdata.value = {};
      attrs.unshift(cloneDeep(proxy.$.appContext.$misAttrs[`mis-${props.misName}`].default));
      proxy.$.appContext.components[props.activeRenderer].mixins.forEach(item => {
        visibleTabs[tabs[item.name]] = true;
      });
      if (proxy.$formItems.includes(`mis-${props.misName}`)) {
        visibleTabs[tabs.visible] = true;
      }
      let visibleNodeType = false;
      for (let i = 0; i < nodeList.length; i++) {
        let item = nodeList[i];
        if (proxy.$.appContext.components[props.activeRenderer].mixins[0].props.hasOwnProperty(item)) {
          visibleNodeType = true;
          visibleNodes[item] = true;
          childType.value = item;
        } else {
          visibleNodes[item] = false;
        }
      }
      visibleTabs['4'] = visibleNodeType;
      updateForm();
    });
    onMounted(() => {
      proxy.$eventHub.$on('mis-component:attrs-editor', onEditDeep);
    });
    onBeforeUnmount(() => {
      proxy.$eventHub.$off('mis-component:attrs-editor', onEditDeep);
      formdata.value = {};
      attrs.shift();
    });

    return {
      beforeLeave,
      onTabChange,
      submitForm,
      showChildNode,
      childFormdata,
      activeTab,
      childType,
      formdata,
      controls,
      childControls,
      loading,
      visibleTabs,
      visibleNodes,
      attrs,
    };
  },
});
</script>
